import React from 'react';
import * as firebase from 'firebase';
// import { on } from 'cluster';
export class Login extends React.Component
{
    constructor(props){
        super(props);
        this.state = {msg:''};
      }
      clear(){
          this.refs.uid.value='';
          this.refs.pwd.value='';
      }
      add()
      {
          var userid = this.refs.uid.value;
          var pwd = this.refs.pwd.value;
          var userObject={"userid":userid,"pwd":pwd};
          var promise = firebase.database().ref("users/"+userid).set(userObject); //badle me promise return karta he
          promise.then(data=>{
            this.setState({msg:'User Added....'});
          }).catch(err=>{
              this.setState({msg:'Error in User adding....'});
          })
      }
      doLogin(){
          var userid = this.refs.uid.value;
          var pwd = this.refs.pwd.value;
        //   var user = firebase.database.ref("users/"+userid);
        //   user.on('value',(snapshot)=>{

        //   })
          
          if(userid==pwd){
              this.setState({msg: 'Welcome ' + userid});
              this.props.updateLoginStatus(true,userid);
               
          }
          else{
            this.setState({msg: 'Invalid Username or Password'});
            this.props.updateLoginStatus(false,userid);
           
          }
      }
    render()
    {
        return(
            <div>
                <h2 className="alert-info">Login Page</h2>
                <h3 className="alert-success">{this.state.msg}</h3>
                <div className="form-group">
                <label htmlFor="">Username</label>
                 <input id="userid" ref="uid" className="form-control" type="text" placeholder="Enter your Login ID"/>
                </div>
                <div className="form-group">
                <label htmlFor="">Password</label>&nbsp;
                 <input type="password" ref="pwd" className="form-control" placeholder="Enter your Password"/>
                 <div className="form-group"><br/>
                    <button onClick={this.doLogin.bind(this)} className="btn btn-primary">Login</button>&nbsp;
                    <button onClick={this.add.bind(this)} className ="btn btn-success">Register</button>&nbsp;
                 </div>
                 <div className="form-group">
                    <button onClick={this.clear.bind(this)} className="btn btn-danger">Clear</button>
                 </div>
                </div>
            </div>
        );
    }
}